import { useSphere } from "@react-three/cannon";
import { useRef } from "react";
export default function CannonSphere({ children, args, mass, ...props }) {
  const [ref, api] = useSphere(
    () => ({
      args: args || 1,
      mass: mass || 10,
      ...props,
    }),
    useRef(null)
  );
  return (
    <group
      ref={ref}
      onClick={(e) => {
        api.applyImpulse([0, 105, 0], [0, 3, 0]);
      }}
    >
      {children}
    </group>
  );
}
