import React, { Suspense } from "react";
import {
  OrbitControls,
  Loader,
  Stars,
  PerspectiveCamera,
} from "@react-three/drei";
import { Canvas } from "@react-three/fiber";
import { space, transitionViewIfSupported } from "../utils/";
import {
  EffectComposer,
  DepthOfField,
  Bloom,
  Noise,
  Vignette,
} from "@react-three/postprocessing";
import Planet from "../three/planet";
import TextureOptions from "../hooks/TextureOptioins";
import { useNavigate } from "react-router-dom";
import { Physics } from "@react-three/cannon";
import CannonPlane from "../cannon/plane";
import CannonSphere from "../cannon/shpere";
import CannonBox from "../cannon/box";
import Ship from "../three/Ship";
import SunRise from "../three/SurRise";
import CreateText from "../three/CreateText";
import jsonData from '../json/data.json'
export default function SpaceJam() {
  const { menu } = jsonData;
  const star_factor = 5000;
  const navigate = useNavigate();
  return (
    <div className="moon flex h-screen w-screen flex-col items-center justify-center">
      <Suspense fallback={<Loader />}>
        <Canvas performance={{ min: 0.5 }} flat linear shadows>
          <PerspectiveCamera
            makeDefault
            fov={75}
            far={1000}
            near={0.01}
            position={[0, space(2), space(15)]}
            rotation={[space(-.1),space(-.02),0]}
            aspect={window.innerWidth / window.innerHeight}
          />
          <ambientLight color="white" intensity={0.25} />
          <directionalLight
            intensity={5}
            position={[-space(2.4), space(3), -space(4.73)]}
            color="#22bad6"
            castShadow
          />
          <SunRise
            turbidity={0.01}
            azimuth={0.2}
            inclination={0.9}
            mieCoefficient={0.091}
            mieDirectionalG={0.4}
            rayleigh={0.01}
          />
          <fog color="black" attach="fog" near={4} far={1000} />
          <Stars
            radius={0.1}
            depth={1000}
            count={star_factor * 3}
            factor={0}
            saturation={10}
            fade
          />

          <OrbitControls
            minPolarAngle={space(0.24)}
            maxPolarAngle={space(0.4)}
            maxAzimuthAngle={space(0.5)}
            minAzimuthAngle={space(-0.5)}
            maxDistance={space(20)}
            minDistance={space(12)}
            enablePan={false}
          />
          <Physics
            defaultContactMaterial={{
              contactEquationRelaxation: 0.4,
              friction: 1e-3,
            }}
            gravity={[0, -12, 0]}
            allowSleep
          >
            <CannonPlane
              rotation={[-space(0.5), 0, 0]}
              position={[0, space(-25), 0]}
              userData={{ id: "floor" }}
            >
              <mesh receiveShadow>
                <planeGeometry args={[1000, 1000]} />
                <meshLambertMaterial opacity={0} transparent={true} />
              </mesh>
            </CannonPlane>
            <group rotation={[0, space(4), 0]}>
              <CannonSphere
                args={[10]}
                position={[0, 10, space(-3)]}
              >
                <group
                  position={[0, 0, 0]}
                >
                  <Planet
                    bumpScale={0.3}
                    args={[10, 64, 64]}
                    {...TextureOptions("textures/moon")}
                    animate={({ meshRef, delta, clock }) => {
                      if (meshRef?.current) {
                        meshRef.current.rotation.set(
                          0,
                          space(clock.oldTime / 90000),
                          0
                        );
                      }
                    }}
                  />
                </group>
              </CannonSphere>
              <CannonBox
                mass={100}
                args={[80, 10, 5]}
                position={[0, space(14), 0]}
              >
                <CreateText
                  textstring="Moon Bowl"
                  fontSize={10}
                  fontHeight={3}
                  position={[space(-15), 0, 0]}
                />
              </CannonBox>
              <CannonSphere
                args={[60]}
                position={[0, 0, space(-130)]}
                type="Static"
              >
                <Planet
                  bumpScale={0.3}
                  args={[60, 64, 64]}
                  {...TextureOptions("textures/earth")}
                  animate={({ meshRef, delta, clock }) => {
                    if (meshRef?.current) {
                      meshRef.current.rotation.set(
                        0,
                        space(clock.oldTime / 90000),
                        0
                      );
                    }
                  }}
                />
                <group
                  position={[0, space(18), 0]}
                  onClick={() => {
                    transitionViewIfSupported(navigate(menu[1].route));
                  }}
                >
                  <CreateText
                    textstring="<-Back"
                    fontSize={10}
                    fontHeight={0.3}
                    position={[space(3), 0, 0]}
                    hover="#ff99cc"
                  />
                </group>
              </CannonSphere>
            </group>
            <group>
              <CannonBox args={[35, 10, 40]} position={[0, 0, -space(102)]}>
                <group>
                  <Ship />
                </group>
              </CannonBox>
            </group>
          </Physics>
          <EffectComposer>
            <DepthOfField
              focusDistance={30}
              focalLength={500}
              bokehScale={7.5}
              height={10}
            />
            <Bloom luminanceThreshold={40} luminanceSmoothing={9} height={30} />
            <Noise opacity={0.05} />
            <Vignette eskil={true} offset={0.0} darkness={1.1} />
          </EffectComposer>
        </Canvas>
      </Suspense>
    </div>
  );
}
